import React, { useEffect, useState } from 'react';
import './VideoWithPopups.css';

const VideoWithPopups = ({ videoSrc, popups, videoId }) => {
  const [currentPopupIndex, setCurrentPopupIndex] = useState(0);
  const [isPopupVisible, setIsPopupVisible] = useState(false);

  useEffect(() => {
    const video = document.getElementById(videoId);

    const handleTimeUpdate = () => {
      if (currentPopupIndex < popups.length && video.currentTime >= popups[currentPopupIndex].time) {
        setIsPopupVisible(true);
        video.pause();

        setTimeout(() => {
          setIsPopupVisible(false);
          video.play();
          setCurrentPopupIndex(prevIndex => prevIndex + 1);
        }, 5500); // Show popup for 4 seconds
      }
    };

    const handleVideoEnded = () => {
      setCurrentPopupIndex(0); // Reset popup index
      setIsPopupVisible(false); // Hide popup when the video loops
    };

    video.addEventListener('timeupdate', handleTimeUpdate);
    video.addEventListener('ended', handleVideoEnded);

    return () => {
      video.removeEventListener('timeupdate', handleTimeUpdate);
      video.removeEventListener('ended', handleVideoEnded);
    };
  }, [currentPopupIndex, popups, videoId]);

  return (
    <div className="video-container">
      <video id={videoId} width="800" controls loop>
        <source src={videoSrc} type="video/mp4" />
        Your browser does not support the video tag.
      </video>

      {isPopupVisible && (
        <div
          className={`popup ${isPopupVisible ? 'show' : ''}`}
          style={{
            top: popups[currentPopupIndex]?.top || 'auto',
            bottom: popups[currentPopupIndex]?.bottom || 'auto',
            left: popups[currentPopupIndex]?.left || '50%',
            right: popups[currentPopupIndex]?.right || 'auto',
            transform: popups[currentPopupIndex]?.transform || 'translateX(-50%)',
          }}
        >
          {currentPopupIndex < popups.length && (
            <>
              {popups[currentPopupIndex].emoji ? (
                <div className="emoji" role="img" aria-label="emoji">{popups[currentPopupIndex].emoji}</div>
              ) : (
                <img src={popups[currentPopupIndex].imgSrc} alt="Popup" id="popupImage" />
              )}
              <div id="popupTitle">{popups[currentPopupIndex].title}</div>
              <div id="popupText">{popups[currentPopupIndex].message}</div>
            </>
          )}
        </div>
      )}
    </div>
  );
};

export default VideoWithPopups;
